<template>
  <v-row
    align="center"
    justify="center"
    style="height: calc(100vh - 50px)"
  >
    <div>
      <div class="text-center pb-5">
        <span class="text-h2 font-weight-bold">
          {{ site_name }}
        </span>
      </div>
    <v-sheet>

      <Language width="80px" class="Language" />

      <v-card
        class="px-5 py-12"
        max-width="400"
        min-width="320"
        min-height="355"
      >
        <v-window v-model="step">
          <!-------------------------[ USERNAME ]-------------------------->
          <v-window-item :value="1">
            <v-form ref="form_username" @submit.prevent="check_username">
              <v-card-title>
                <span class="display-1 font-weight-bold">
                  Username
                </span>
                <v-spacer />
                <v-icon large>mdi-account</v-icon>
              </v-card-title>
              <v-card-text class="pt-5 pb-0">
                <v-text-field
                  ref="username"
                  v-model="username"
                  label="Username"
                  outlined
                  validate-on-blur
                  clearable
                  class="mb-n2"
                  :rules="[v => !!v || 'val_username']"
                >
                  <template v-slot:prepend-inner>
                    <v-icon>mdi-account</v-icon>
                    <v-divider
                      class="mx-2"
                      vertical
                    />
                  </template>
                </v-text-field>
                <span class="caption grey--text text--darken-1">
                  Login.sub_username
                </span>

              </v-card-text>

              <v-divider class="mt-1" />

              <v-card-actions class="px-5 mt-2">
                <v-btn
                  text
                  small
                  class="grey--text caption"
                  @click="step=3"
                >
                  <v-icon
                    small
                    class="mr-1"
                  >
                    mdi-account-question-outline
                  </v-icon>
                  Lost_pass
                </v-btn>
                <v-spacer />
                <v-btn
                  class="px-5"
                  color="primary"
                  type="submit">
                  next
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-window-item>
          <!-------------------------[ /USERNAME ]-------------------------->

          <!-------------------------[ PASSWORD ]-------------------------->
           <v-window-item :value="2">
            <v-form ref="form_password" @submit.prevent="check_password">
              <v-card-title>
                <span class="display-1 font-weight-bold">
                  Password
                </span>
                <v-spacer />
                <v-icon large>mdi-account-key</v-icon>
              </v-card-title>
              <v-card-text class="pt-5 pb-0">
                <v-text-field
                  ref="password"
                  v-model="password"
                  label="Password"
                  min="8"
                  outlined
                  validate-on-blur
                  :append-icon="e1?'mdi-eye':'mdi-eye-off'"
                  :type="e1?'text':'password'"
                  :rules="[
                  v => !!v || 'val_password',
                  v=>v.length>= 8 || 'val_min8']"
                  counter
                  class="mb-n2"
                  @click:append="()=>(e1=!e1)"
                >
                  <template v-slot:prepend-inner>
                    <v-icon>mdi-key</v-icon>
                    <v-divider
                      class="mx-2"
                      vertical
                    />
                  </template>
                </v-text-field>
                <span class="caption grey--text text--darken-1">
                  Login.sub_password
                </span>

              </v-card-text>

              <v-divider class="mt-1"></v-divider>

              <v-card-actions class="px-5 mt-2">
                <v-btn
                  text
                  @click="Back"
                >
                  <v-icon class="mr-1">
                    mdi-arrow-left-bold-circle-outline
                  </v-icon>
                  Back
                </v-btn>
                <v-spacer />
                <v-btn
                  class="px-5"
                  color="primary"
                  type="submit">
                  LOGIN
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-window-item>
          <!-------------------------[ /PASSWORD ]-------------------------->

          <!---------------------------[ EMAIL ]---------------------------->
          <v-window-item :value="3">
            <v-card-title>
              <span class="display-1 font-weight-bold">
                Reset Password
              </span>
              <v-spacer />
              <v-icon large>
                mdi-at
              </v-icon>
            </v-card-title>
            <v-form ref="form_email">
              <v-card-text class="pt-5 pb-0">
                <v-text-field
                  ref="email"
                  v-model="email"
                  label="Email"
                  placeholder="EmailAdrass"
                  clearable
                  outlined
                  validate-on-blur
                  class="mb-n2"
                  :rules="[
                    v => !!v || 'val_data',
                    v => /.+@.+\..+/.test(v) || 'Login.val_email'
                  ]"
                >
                  <template v-slot:prepend-inner>
                    <v-icon>mdi-at</v-icon>
                    <v-divider
                      class="mx-2"
                      vertical
                    />
                  </template>
                </v-text-field>
                <span class="caption grey--text text--darken-1">
                  Login.sub_email
                </span>
              </v-card-text>

              <v-divider class="mt-1" />

              <v-card-actions class="px-5 mt-2">
                <v-btn
                  text
                  @click="Back"
                >
                  <v-icon class="mr-1">
                    mdi-arrow-left-bold-circle-outline
                  </v-icon>
                  Back
                </v-btn>
                <v-spacer />
                <v-btn
                  color="primary"
                  @click="step++"
                >
                  Send
                  <v-icon class="ml-1">
                    mdi-send-outline
                  </v-icon>
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-window-item>
          <!---------------------------[ EMAIL ]---------------------------->

          <!------------------------[ RESET PASSWORD ]---------------------->
          <v-window-item :value="4">
            <v-card-title>
              <span class="display-1 font-weight-bold">
                Login.R_title
              </span>
            </v-card-title>
            <v-form ref="form_reset">
              <v-card-text class="pt-5 pb-0">
                <div class="text--secondary body-1">
                  Login.code_hint
                </div>
                <v-row
                  no-gutters
                  class="mb-n4"
                >
                  <v-col cols="2">
                    <v-text-field
                      id="code-1"
                      ref="code1"
                      v-model="code.c1"
                      v-mask="code_mask"
                      placeholder="0"
                      outlined
                      class="code-reset font-weight-black"
                      single-line
                      :rules="[v => !!v || '']"
                      @keyup="enter_code(code.c1,$refs.code2,$refs.code1)"
                    />
                  </v-col>

                  <v-col cols="2">
                    <v-text-field
                      id="code-2"
                      ref="code2"
                      v-model="code.c2"
                      v-mask="code_mask"
                      placeholder="0"
                      outlined
                      class="code-reset font-weight-black"
                      single-line
                      :rules="[v => !!v || '']"
                      @keyup="enter_code(code.c2,$refs.code3,$refs.code1)"
                    />
                  </v-col>

                  <v-col cols="2">
                    <v-text-field
                      id="code-3"
                      ref="code3"
                      v-model="code.c3"
                      v-mask="code_mask"
                      placeholder="0"
                      outlined
                      class="code-reset font-weight-black"
                      single-line
                      :rules="[v => !!v || '']"
                      @keyup="enter_code(code.c3,$refs.code4,$refs.code2)"
                    />
                  </v-col>

                  <v-col cols="2">
                    <v-text-field
                      id="code-4"
                      ref="code4"
                      v-model="code.c4"
                      v-mask="code_mask"
                      placeholder="0"
                      outlined
                      class="code-reset font-weight-black"
                      :rules="[v => !!v || '']"
                      @keyup="enter_code(code.c4,$refs.code5,$refs.code3)"
                    />
                  </v-col>

                  <v-col cols="2">
                    <v-text-field
                      id="code-5"
                      ref="code5"
                      v-model="code.c5"
                      v-mask="code_mask"
                      placeholder="0"
                      outlined
                      class="code-reset font-weight-black"
                      :rules="[v => !!v || '']"
                      @keyup="enter_code(code.c5,$refs.code6,$refs.code4)"
                    />
                  </v-col>

                  <v-col cols="2">
                    <v-text-field
                      id="code-6"
                      ref="code6"
                      v-model="code.c6"
                      v-mask="code_mask"
                      placeholder="0"
                      outlined
                      class="code-reset font-weight-black"
                      single-line
                      :rules="[v => !!v || '']"
                      @keyup="enter_code(code.c6,$refs.new_password,$refs.code5)"
                    />
                  </v-col>
                </v-row>

                <v-text-field
                  ref="new_password"
                  v-model="new_password"
                  placeholder="'Login.Pass_New"
                  :rules="[
                    v=>!!v || 'Login.val_password',
                    v => v.length >= 8 || 'Login.val_min8']"
                  :append-icon="show_pass ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show_pass ? 'text' : 'password'"
                  min="8"
                  filled
                  counter
                  validate-on-blur
                  @click:append="show_pass = !show_pass"
                >
                  <template v-slot:prepend-inner>
                    <v-icon>mdi-key</v-icon>
                    <v-divider
                      class="mx-2"
                      vertical
                    />
                  </template>
                </v-text-field>

                <v-text-field
                  ref="new_pass_try"
                  v-model="new_pass_try"
                  placeholder="'Login.Pass_again"
                  :rules="[
                    v => !!v || 'Login.val_password',
                    v => v.length >= 8 || 'Login.val_min8',
                    confirm_password || 'Login.some_value'
                  ]"
                  :append-icon="show_pass_try ? 'mdi-eye' : 'mdi-eye-off'"
                  :type="show_pass_try ? 'text' : 'password'"
                  min="8"
                  validate-on-blur
                  filled
                  counter
                  @click:append="show_pass_try = !show_pass_try"
                >
                  <template v-slot:prepend-inner>
                    <v-icon>mdi-key</v-icon>
                    <v-divider
                      class="mx-2"
                      vertical
                    />
                  </template>
                </v-text-field>
              </v-card-text>

              <v-divider></v-divider>

              <v-card-actions class="px-5 my-2">
                <v-btn
                  text
                  @click="Back"
                >
                  <v-icon class="mr-1">
                    mdi-arrow-left-bold-circle-outline
                  </v-icon>
                  Back
                </v-btn>
                <v-spacer />
                <v-btn
                  large
                  color="primary"
                  type="submit"
                >
                  Save
                  <v-icon class="ml-1">
                    mdi-content-save-outline
                  </v-icon>
                </v-btn>
              </v-card-actions>
            </v-form>
          </v-window-item>
          <!------------------------[ /RESET PASSWORD ]---------------------->

        </v-window>
      </v-card>
    </v-sheet>
    </div>

    <Snackbar ref="Alert" />

    <Loading ref="loading" text="Home.Please_wait" />
  </v-row>
</template>

<script>
import Snackbar from '@/components/Snackbar'
import Loading from '@/components/Loading'
import Language from '@/components/Language'
import axios from '@/plugins/axios'
import jwt from 'jsonwebtoken'
export default {
  components: { Language, Snackbar, Loading },
  data: () => ({
    site_name: 'NPOS',
    step: 1,
    username: '',
    password: '',
    e1: false,
    email: '',
    code: {
      c1: '',
      c2: '',
      c3: '',
      c4: '',
      c5: '',
      c6: ''
    },
    code_mask: '#',
    new_password: '',
    new_pass_try: '',
    show_pass: false,
    show_pass_try: false
  }),
  computed: {
    confirm_password () {
      return this.new_pass_try === this.new_password
    }
  },
  methods: {
    Back () {
      this.step = 1
      this.username = ''
      setTimeout(() => {
        this.$refs.username.focus()
      }, 20)
    },
    // --------------------------------------------------------------

    // --------------------------------------------------------------
    async check_username () {
      if (this.$refs.form_username.validate()) {
        // const DATA = this.DataForm({ username: this.username })
        this.$refs.loading.SET_Loading(true)
        await axios.post('/login/username', { username: this.username })
          .then((res) => {
            setTimeout(() => {
              if (res.data.success) {
                this.step = 2
                setTimeout(() => {
                  this.$refs.password.focus()
                }, 500)
              } else {
                this.username = ''
                this.$refs.username.focus()
                this.$refs.Alert.SHOW('error', 'Login.Exit_user')
              }
              this.$refs.loading.SET_Loading(false)
            }, 1000)
          })
      }
    },
    // --------------------------------------------------------------

    // --------------------------------------------------------------
    async check_password () {
      if (this.$refs.form_password.validate()) {
        const DATA = { username: this.username, password: this.password }
        this.$refs.loading.SET_Loading(true)
        await axios.post('/login/password', DATA).then((res) => {
          setTimeout(() => {
            if (res.data.success) {
              const decoded = jwt.decode(res.data.auth)
              console.log(decoded)
              this.$store.commit('SET_LOGIN', res.data.auth)
              this.$cookies.set('loggedIn', res.data.auth)
              this.$router.push({ name: 'Home' })
            } else {
              this.password = ''
              this.$refs.password.focus()
              this.$refs.Alert.SHOW('error', 'Login.P_no_match')
            }
            this.$refs.loading.SET_Loading(false)
          }, 1000)
        })
      }
    },
    // --------------------------------------------------------------

    // --------------------------------------------------------------
    enter_code (code, co1, co2) {
      const evt = code || window.event
      const Code = evt.which ? evt.which : evt.keyCode
      if (Code > 31 && (Code < 48 || Code > 57) && Code !== 46) {
        evt.preventDefault()
      } else if (code === '') {
        co2.focus()
      } else {
        co1.focus()
      }
    }
  }
  // --------------------------------------------------------------

}
</script>

<style>
.Language {
  position: absolute;
  top: 10px;
  right: 20px;
}
.code-reset {
  border-radius: 0;
  margin-left: 0.5px !important;
  margin-right: 0.5px !important;
}
input#code-1,
input#code-2,
input#code-3,
input#code-4,
input#code-5,
input#code-6 {
  text-align: center;
  font-size: 2rem;
}
</style>
