import Home from '@/views/Home'
import Users from '@/views/Users'
import Stores from '@/views/Stores'
import Sales from '@/views/Sales'
import Login from '@/views/Login'
import auth from '@/middleware/auth'
import guest from '@/middleware/guest'

export default [
  {
    path: '',
    name: 'Home',
    component: Home,
    meta: {
      layout: 'Admin',
      middleware: [auth]
    }
  },
  {
    path: 'dashboard',
    name: 'Dashboard',
    component: Home,
    meta: {
      layout: 'Admin',
      middleware: [auth]
    }
  },
  {
    path: 'users',
    name: 'Users',
    component: Users,
    meta: {
      layout: 'Admin',
      middleware: [auth]
    }
  },
  {
    path: 'stores',
    name: 'Stores',
    component: Stores,
    meta: {
      layout: 'Admin',
      middleware: [auth]
    }
  },
  {
    path: 'sales',
    name: 'Sales',
    component: Sales,
    meta: {
      layout: 'Admin',
      middleware: [auth]
    }
  },
  {
    path: 'login',
    name: 'Login',
    component: Login,
    meta: {
      middleware: [guest]
    }
  },
  {
    path: '*',
    name: 'Error',
    meta: {
      layout: 'error'
    }
  }
]
