<template>
  <v-select
    :items="lang"
    v-model="language"
    single-line
    item-text="text"
    item-value="src"
    return-object
    persistent-hint
    v-on:change="change_Language(language.src)"
    class="pa-2"
    :class="['elevation-'+elevation]"
    style="height: 3.1rem;"
    :style="{'max-width': width}"
  ></v-select>
</template>
<script>
import Vue from 'vue'
export default {
  name: 'Language',
  props: {
    width: {
      type: String,
      default: null
    },
    elevation: {
      type: String,
      default: '5'
    }
  },
  data: () => ({
    language: {
      text: Vue.$cookies.get('locale') === 'th' ? 'TH' : 'EN',
      src: Vue.$cookies.get('locale') === 'th' ? 'th' : 'en'
    },
    lang: [
      { text: 'EN', src: 'en' },
      { text: 'TH', src: 'th' }
    ]
  }),
  methods: {
    change_Language (locale) {
      this.$cookies.set('locale', locale)
      this.$i18n.locale = this.$cookies.get('locale')
      this.$router.replace({ params: { locale } })
    }
  }
}
</script>
