import Vue from 'vue'
import Vuex from 'vuex'
import VueCookies from 'vue-cookies'
Vue.use(VueCookies)
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {
      loggedIn: Vue.$cookies.get('loggedIn')
    }
  },

  getters: {
    auth (state) {
      return state.user
    }
  },
  mutations: {
    SET_LOGIN (state, data) {
      state.user.loggedIn = data
    }
  },
  actions: {
  },
  modules: {
  }
})
